body {
  text-align: center;
  background-color: #f9f9f9;
  font-family: Arial, sans-serif;
}

#game-container {
  background-color: #fff;
  border: 2px solid #ccc;
  border-radius: 10px;
  margin: 50px auto;
  padding: 20px;
  display: inline-block;
}

#grid {
  grid-template-rows: repeat(8, 50px);
  grid-template-columns: repeat(8, 50px);
  gap: 2px;
  display: grid;
}

#error {
  color: red;
}

.cell {
  cursor: pointer;
  background-color: #f0f0f0;
  border: 1px solid #ddd;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  font-size: 1rem;
  display: flex;
}

.cell.occupied {
  cursor: default;
  background-color: #e0e0e0;
}

.hidden {
  display: none !important;
}

@keyframes bobDance {
  0%, 100% {
    transform: translate(0)rotate(0);
  }

  10% {
    transform: translate(-2px, -5px)rotate(-5deg);
  }

  20% {
    transform: translate(3px, -3px)rotate(5deg);
  }

  30% {
    transform: translate(-4px, 2px)rotate(-8deg);
  }

  40% {
    transform: translate(4px, -4px)rotate(8deg);
  }

  50% {
    transform: translate(0)rotate(0);
  }

  60% {
    transform: translate(5px, 3px)rotate(6deg);
  }

  70% {
    transform: translate(-3px, -2px)rotate(-6deg);
  }

  80% {
    transform: translate(2px, 4px)rotate(4deg);
  }

  90% {
    transform: translate(-2px, -1px)rotate(-4deg);
  }
}

@keyframes go {
  50% {
    transform: scale(1.5);
  }
}

.post-game {
  text-align: center;
  animation: 1.5s ease-in-out infinite bobDance, .5s ease-in-out infinite go;
}

.post-game-text {
  text-align: center;
  animation: 1.4s ease-in-out infinite bobDance, .6s ease-in-out infinite go;
}
